.dialog-section {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.full-width {
  width: 100%;
}

.info-box {
  display: flex;
  gap: 12px;
  padding: 16px;
  background: linear-gradient(135deg, rgba(255, 152, 0, 0.08), rgba(255, 193, 7, 0.08));
  border-radius: 8px;
  border: 1px dashed rgba(255, 152, 0, 0.3);
  margin-top: 16px;

  mat-icon {
    color: #ff9800;
    font-size: 24px;
    width: 24px;
    height: 24px;
    flex-shrink: 0;
  }

  .info-content {
    flex: 1;
    font-size: 13px;
    line-height: 1.6;
    color: #555;

    strong {
      display: block;
      margin-bottom: 8px;
      color: #333;
      font-size: 14px;
    }

    ol {
      margin: 8px 0 0 0;
      padding-left: 20px;

      li {
        margin-bottom: 6px;
        color: #666;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}

mat-dialog-content {
  min-width: 500px;
  max-height: 600px;
  overflow-y: auto;
}
